import React from 'react';
import './App.scss';
import { Clock } from './Clock';

type State = {
  clockName: string;
  currentTime: string;
  hasClock: boolean;
};

type Props = {};

function getRandomName(): string {
  const value = Date.now().toString().slice(-4);

  return `Clock-${value}`;
}

export class App extends React.Component<Props, State> {
  state: State = {
    clockName: 'Clock-0',
    currentTime: new Date().toUTCString().slice(-12, -4),
    hasClock: true,
  };

  timerId = 0;

  currentTime = 0;

  componentDidMount(): void {
    document.addEventListener('click', this.handleDocumentClick);
    document.addEventListener('contextmenu', this.handleDocumentContextMenu);

    this.timerId = window.setInterval(() => {
      const oldName = this.state.clockName;

      this.setState({ clockName: getRandomName() });
      // eslint-disable-next-line no-console
      console.debug(`Renamed from ${oldName} to ${this.state.clockName}`);
    }, 3300);

    this.currentTime = window.setInterval(() => {
      const currentTime = new Date().toUTCString().slice(-12, -4);

      this.setState({ currentTime });
      // eslint-disable-next-line no-console
      console.info(currentTime);
    }, 1000);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.handleDocumentClick);
    document.removeEventListener('contextmenu', this.handleDocumentContextMenu);
  }

  handleDocumentClick = () => {
    this.setState({ hasClock: true });
  };

  handleDocumentContextMenu = (event: MouseEvent) => {
    event.preventDefault();
    this.setState({ hasClock: false });
  };

  handleClick = () => {
    this.setState({ hasClock: true });
  };

  handleRightClick = () => {
    this.setState({ hasClock: false });
  };

  render() {
    const { clockName, currentTime, hasClock } = this.state;

    return (
      <div className="App">
        <h1>React clock</h1>
        {hasClock && <Clock name={clockName} time={currentTime} />}
      </div>
    );
  }
}
